import './Links.scss';
import $ from 'jquery';
import { db } from './utils/Firebase';
import { Visitas } from './utils/Helpers';
import { streamer } from './utils/Config';
import { Capitalize } from './utils/Helpers';
import { useEffect, useState } from 'react';
import { datosFirebaseUsuario } from './utils/Firebase';
import Header from './Header';
import Footer from './Footer';

const Links = () => {
    const [enlaces, setEnlaces] = useState([]);

    useEffect(() => {
        datosFirebaseUsuario()
        .then((data) => {
            $('#nota').html(data.note);
        })

        const arrayDatos = db.collection("users").doc(streamer).collection("buttons").orderBy("timestamp")
        .onSnapshot((arrayEnlaces) => {
            $('#buttons').empty()
            setEnlaces(enlace => enlace.concat(arrayEnlaces.docs))
        }, (error) => {
            console.log(error)
        });

        let contador = 0;
        obtainStatus1()
        let minutos = 1
        let ms = minutos * 60 * 1000
        
        let rellamadas = setInterval(() => {
            obtainStatus1()
        }, ms)

        $('.cargando').show()
        function obtainStatus1() {
            contador++
            if (contador >= 5) {clearInterval(rellamadas)}
            $.get({
                url: `https://twitch-check.glitch.me/${streamer}`,
                cache: false,
                success : function(response){
                    if (response) {
                        $('.cargando').hide();
                        if (response.online) {
                            $('#texto-live').html('ONLINE: ' + response.stream_viewers + '<i class="fa fa-eye ml-1"></i>')
                            $('#icon-twitch').addClass('logo-animation')
                            $('#texto-live').removeClass('texto-offline').addClass('texto-online')
                            $('#punto-live').removeClass('punto-offline').addClass('punto-online')
                        } else {
                            $('#texto-live').text('OFFLINE')
                            $('#icon-twitch').removeClass('logo-animation')
                            $('#texto-live').removeClass('texto-online').addClass('texto-offline')
                            $('#punto-live').removeClass('punto-online').addClass('punto-offline')
                        }
                    } else {
                        setTimeout(() => {
                            obtainStatus1()
                        },3000)
                    }
                }
            })
        }

        return () => arrayDatos()        
    }, []);

    return (
        <>
            <Header />
            <div className="row mt-2">
                <div className="nota" id="nota"></div>
            </div>
            <div id="buttons">
                {
                    enlaces.length > 0 ? (
                        enlaces.map((enlace,index) => (
                            <div className="row" key={index}>
                                <a className="boton" rel="noreferrer" onClick={() => Visitas(enlace.data().icon)} href={enlace.data().url} target="_blank">
                                    <div className="col">
                                        {/* <img id={'icon-'+enlace.data().icon} className="icon" alt={enlace.data().name} src={process.env.PUBLIC_URL + `/img/${enlace.data().icon}.png`} /> */}
                                        <div className="d-flex align-items-center">
                                            <div className="icon d-flex justify-content-center align-items-center"><i id={'icon-'+enlace.data().icon} className={"icon-text fab fa-"+enlace.data().icon}></i></div>
                                            <span className="boton-name">{Capitalize(enlace.data().name)}</span>
                                        </div>
                                    </div>
                                </a>
                            </div>
                        ))
                    ) : (null)
                }
            </div>
            <Footer />
        </>
    )
}

export default Links;